    }
    .ManageItems {
      padding: 15px 60px;
      .manageLink {
        text-decoration: none;
        color: #7e7e7e;
        font-weight: 500;
        font-size: 15px;
        display: block;
        margin-bottom: 12px;
      }
    }
  }
  .HistoryLogInCom {
    padding: 20px;
    text-align: center;
    width: 65%;
    margin: 0 auto;
    margin-right: 35%;
    h2 {
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 20px;
      text-align: left;
    }
    p {
      text-align: center;
      font-size: 14px;
      font-weight: 400;
    }
  }
}

